@mixin circle($size,$bgcolor) {
    display: inline-block;
    width: $size;
    height: $size;
    border: 1px solid #fff;
    margin: 0 5px;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    border-radius: 50%;
    background: $bgcolor;
}

@mixin bgSetting($url) {
    background-image: url("../Images/#{$url}");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

%clearfix {
    &:after {
        display: block;
        content: "";
        clear: both;
    }
}

%vertical-center {
    position: relative;
    top: 50%;
    transform: translateY(-50%);
}

%hide-text {
    text-indent: 110%;
    white-space: nowrap;
    overflow: hidden;
}