@import 'reset';
@import 'mixin';
@import 'grid';
$mainColor: #0275D8;
$wrapColor: #BEE1FF;

body {
    font-size: 16px;
    font-family: "Roboto","Microsoft JhengHei","PingFangTC-Regular";
    .wrap {
        margin: 0 auto;
        width: 465px;
        border-top: 8px solid $mainColor;
        .stepBar {
            width: 250px;
            height: 16px;
            margin: 0 auto;
            margin-top: 54px;
            > * {
                position: relative;
                & > .nowhere {
                    @include circle(8px, $mainColor);
                    border: solid 2px $mainColor;
                    position: absolute;
                    left: -7px;
                    top: -2px;
                }
                &::before {
                    @include circle(16px, transparent);
                    border: 2px solid $wrapColor;
                    content: "";
                    position: absolute;
                    left: -11px;
                    top: -6px;
                }
                &::after {
                    position: absolute;
                    content: "";
                    left: 9px;
                    top: 1px;
                    width: 70px;
                    height: 2px;
                    border: 1px solid $wrapColor;
                }
            }
            .active::before{
                border: 2px solid $mainColor;
            }
            .step4::after {
                position: initial;
                width: 0;
                height: 0;
                border: none;
            } 
        }
        .welcome {
            margin: 0 auto;
            text-align: center;
            margin-top: 40px;
            h1 {
                font-size: 48px;
                color: #000;
                font-weight: lighter;
            }
            p {
                font-size: 24px;
                font-weight: lighter;
                margin-top: 5px;
            }
        }
        .userInfo {
            margin-top: 24px;
            label {
                font-size: 20px;
                color: #000000;
                font-weight: bolder;
                padding-bottom: 8px;
            }
            .input-group {
                margin-bottom: 24px;
                .form-control {
                    border: 2px solid #000;
                    border-radius: 8px;
                    outline: none;
                    box-shadow: unset;
                }
            }
        }
        .btn-next {
            background: $wrapColor;
            border: 2px solid $wrapColor;
            border-radius: 8px;
            width: 100%;
            height: 52px;
            color: white;
            font-size: 20px;
            margin-top: 16px;
        }
    }
}